body {
  height: 100vh;
  overflow: hidden;
  background: url("../img/—Pngtree—blue carbon background with sport_1200848.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
body.active {
  transition: 0.7s;
}
body.active::before {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  content: "";
  background-color: rgba(0, 0, 0, 0.9);
  transition: 0.7s;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.container {
  max-width: 100vw;
  padding: 20px;
  margin: 0 auto;
}

/* ul */
/* xl */
/* lg */
/* md */
/* sm */
/* xs */
.header {
  max-height: 100px;
  position: absolute;
  min-width: 100vw;
  margin: 0 auto;
  padding: 20px;
}
.header__menu {
  display: flex;
  justify-content: space-between;
}
.header__menu-logo {
  max-height: 100px;
  transition: 0.7s;
}
.header__menu-logo:hover {
  opacity: 0.6;
  transition: 0.7s;
}
.header__menu-link {
  padding: 1em;
  background-color: rgb(255, 230, 230);
  border-radius: 30px;
  transition: 0.7s;
  font-weight: 600;
  color: black;
}
.header__menu-link.active {
  background-color: black;
  color: rgb(255, 230, 230);
  outline: 2px solid rgb(255, 230, 230);
  transition: 0.7s;
}
.header__menu-link:hover {
  background-color: black;
  color: rgb(255, 230, 230);
  outline: 2px solid rgb(255, 230, 230);
  transition: 0.7s;
}
.header__menu-img {
  max-height: 100px;
  object-fit: cover;
  border-radius: 100px;
  border: 2px solid rgb(255, 230, 230);
}
.header__menu-actions {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 767.8px) {
  .header__menu-actions {
    flex-direction: column;
    gap: 50px;
  }
}
.dashboard {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-areas: "dashboard-menu dashboard-content";
  width: 100vw;
  justify-items: center;
  height: 500px;
}
.dashboard__menu {
  display: flex;
  color: rgb(255, 230, 230);
  border-right: 2px solid rgb(255, 230, 230);
  grid-area: dashboard-menu;
  padding-right: 20px;
}
.dashboard__menu-actions {
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 30px;
  padding: 0;
}
.dashboard__menu-link {
  color: inherit;
  text-decoration: none;
  padding: 0.5em;
  font-weight: 600;
}
.dashboard__menu-link.active {
  background-color: rgb(255, 230, 230);
  border-radius: 20px;
  color: black;
  transition: 0.7s;
}
.dashboard__menu-link:hover {
  background-color: rgb(255, 230, 230);
  border-radius: 20px;
  color: black;
  transition: 0.7s;
}
.dashboard__content {
  grid-area: dashboard-content;
}

.profile {
  padding: 1em;
  opacity: 0;
  transform: translateX(300%);
}
.profile__action {
  outline: none;
  border: none;
  background-color: rgb(255, 230, 230);
  color: black;
  margin-left: 1em;
  padding: 0.5em;
  border-radius: 20px;
  min-width: 100px;
}
.profile__action:hover {
  background-color: black;
  border-radius: 20px;
  color: rgb(255, 230, 230);
  transition: 0.7s;
}
.profile__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  padding-bottom: 20px;
}
.profile__input {
  border: none;
  outline: 2px solid transparent;
  color: black;
  padding: 0.5em 2em 0.5em 0.5em;
}
.profile.active {
  opacity: 1;
  transform: translateX(0%);
  transition: 0.7s;
}
.profile__add-network {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
}
.profile__add-network.active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 99;
  opacity: 1;
  visibility: visible;
}
.profile__name-network {
  font-weight: 600;
  font-style: italic;
  padding-top: 20px;
}

.options {
  padding: 1em;
  opacity: 0;
  transform: translateX(300%);
}
.options__menu {
  color: rgb(255, 230, 230);
  border: 2px solid rgb(255, 230, 230);
  border-radius: 20px;
  padding: 1em;
}
.options__menu-actions {
  display: flex;
  justify-content: center;
  gap: 30px;
  padding: 0;
}
.options__menu-link {
  color: inherit;
  text-decoration: none;
  padding: 0.5em;
  font-weight: 600;
}
.options__menu-link.active {
  background-color: rgb(255, 230, 230);
  border-radius: 20px;
  color: black;
  transition: 0.7s;
}
.options__menu-link:hover {
  background-color: rgb(255, 230, 230);
  border-radius: 20px;
  color: black;
  transition: 0.7s;
}
.options.active {
  opacity: 1;
  transform: translateX(0%);
  transition: 0.7s;
}
.options__check-balance {
  padding-top: 20px;
}

.modal {
  width: 400px;
  padding: 1em;
  border: 2px solid rgb(255, 230, 230);
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.modal__network {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  align-items: center;
}
.modal__network-input {
  padding: 0.5em;
  border: none;
  outline: none;
  color: black;
}
.modal__submit {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  outline: 2px solid rgb(255, 230, 230);
  border: none;
  background-color: black;
  color: rgb(255, 230, 230);
}
.modal__submit--close {
  width: 396px;
}
.modal__submit:hover {
  background-color: rgb(255, 230, 230);
  color: black;
  transition: 0.7s;
}

.balance__wrap {
  display: flex;
  gap: 30px;
}
.balance__item {
  min-width: 600px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  font-weight: 600;
  padding-top: 20px;
}
.balance__head {
  font-weight: normal;
  font-style: italic;
  border-bottom: 2px solid rgb(255, 230, 230);
}
.balance__action {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  outline: 2px solid rgb(255, 230, 230);
  border: none;
  background-color: black;
  color: rgb(255, 230, 230);
}
.balance__action:hover {
  background-color: rgb(255, 230, 230);
  color: black;
  transition: 0.7s;
}
.balance__message {
  text-align: center;
  font-weight: 600;
}

.form {
  border: 2px solid rgb(255, 230, 230);
  border-radius: 20px;
  min-width: -webkit-min-content;
  min-width: min-content;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  margin: 1em;
}
.form__register, .form__login {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  gap: 20px;
  padding: 1.5em;
  border-bottom: 2px solid rgb(255, 230, 230);
}
@media (max-width: 767.8px) {
  .form__register, .form__login {
    padding: 1em;
  }
}
.form__input {
  min-width: -webkit-min-content;
  min-width: min-content;
  border: none;
  outline: 2px solid transparent;
  color: black;
  padding: 0.5em 2em 0.5em 0.5em;
}
@media (max-width: 767.8px) {
  .form__input {
    padding: 0.25em 1em 0.25em 0.25em;
  }
}
.form__available {
  min-width: -webkit-min-content;
  min-width: min-content;
  border: none;
  color: black;
  padding: 0.5em 2em 0.5em 0.5em;
  outline: 3px solid rgb(0, 240, 192);
  transition: 0.7s;
}
.form__unavailable {
  min-width: -webkit-min-content;
  min-width: min-content;
  border: none;
  color: black;
  padding: 0.5em 2em 0.5em 0.5em;
  outline: 3px solid rgb(183, 21, 21);
  transition: 0.7s;
}
.form__submit {
  align-self: center;
  margin: 1.5em;
  padding: 1em;
  background: rgb(255, 230, 230);
  outline: none;
  border: none;
  color: black;
  border-radius: 20px;
  opacity: 1;
  transition: 0.7s;
}
.form__submit--disabled {
  align-self: center;
  margin: 1.5em;
  padding: 1em;
  background: rgb(255, 230, 230);
  outline: none;
  border: none;
  color: black;
  border-radius: 20px;
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
  transition: 0.7s;
}

.checked-hide {
  position: absolute;
  right: 35px;
  opacity: 0;
  visibility: hidden;
  transition: 0.7s;
}

.checked-visible {
  position: absolute;
  right: 35px;
  opacity: 1;
  visibility: visible;
  transition: 0.7s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgb(255, 230, 230);
  animation: fadeIn 1s ease-in-out;
}
.main__header {
  font-size: 40px;
  text-align: center;
}
.main__content {
  font-size: 30px;
  max-width: 650px;
  text-align: center;
}
.main__form-message {
  font-weight: 600;
  font-size: 20px;
  transition: 0.7s;
}
.main__options {
  display: flex;
  align-items: center;
  border: 2px solid rgb(255, 230, 230);
  border-radius: 20px;
  padding: 1em;
  flex-direction: column;
}
.main__option {
  padding: 1em;
}